import React from "react";
import QAssure from "../assets/qassure.png";
import rlhp from "../assets/rlhp.jpg";
import qg from "../assets/qg.jpg";

export default class AccreditationSection extends React.PureComponent {
  render() {
    return (
        <section className="featured logos">
            <img src={QAssure} alt="QAssure accredited business"/>
            <img src={rlhp} alt="Registered Labour Hire Provider - Queensland"/>
            <img src={qg} alt="Queensland Government"/>
        </section>
    );
  }
}