import React from "react";
import ContactUsSection from "../sections/ContactUsSection";
import RecruitmentSection from "../sections/RecruitmentSection";
import AccreditationSection from "../sections/AccreditationSection";
import jumbo from "../assets/jumbo-recruitment.jpg";
import "../framework/site.scss";

export default class RecruitmentContent extends React.PureComponent {
  render() {
    return (
      <main>
          <section className="intro">
              <img src={jumbo} alt="Recruitment"/>
          </section>
          <RecruitmentSection showHeader={false} />
          <ContactUsSection />
          <AccreditationSection />
      </main>
    );
  }
}