import React from "react";
import "../framework/site.scss";

export interface RecruitmentSectionProps {
    showHeader: boolean
}

export default class RecruitmentSection extends React.PureComponent<RecruitmentSectionProps> {
  render() {
    return (
        <section className="content" id="recruitment">
            {this.props.showHeader ? <h5>Recruitment</h5> : <></>}
            <p>IT professionals are in high demand.</p>

            <p> They're also critical to business success - whether this means via new projects,
                technology refreshes, customer engagement solutions or business as usual.
                As a result of this industry demand, the resource you need is unlikely to be found via traditional advertising or referral programs.</p>
            <p>We know the market. We know the people. We also commit all of our time to finding the right person for you, allowing your team to progress and deliver.</p>
            <p>Talk to us about your needs and how we can help your business grow.</p>
        </section>
    );
  }
}